import React from 'react';
import {getListSelector} from "../selectors";
import {Link} from "react-router-dom";
import { useSelector } from 'react-redux'

const List = () => {
  const list = useSelector(getListSelector);

  return (
    <>
      <ul className="mb-3">
        {list.map(item => (
          <li key={item.id}>
            <Link to={`/vacancies/${item.id}`}>{item.position}</Link>
          </li>
        ))}
      </ul>

      <h4 className="h4">     :</h4>
      <ul>
        <li>    ;</li>
        <li>     ;</li>
        <li>     .</li>
      </ul>
      <p>        <a href="mailto:hr@ncfu.ru">hr@ncfu.ru</a></p>
    </>
  )
};

export default List;
